export default class ResultSearch {
    constructor(dataSearch) {
        this.dataSearch = dataSearch;
    }

    async getResultSearch() {
        const { dataSearch } = this;
        let data;
        const url = `https://www.omdbapi.com/?s=${dataSearch}&apikey=aab7b845`;
        const res = await fetch(url);
        data = await res.json();
        let title; let img; let year;
        let isResponse=false;
        let resultSearch=[];

        isResponse=(data.Response).toLowerCase()

        if (isResponse==='false'){isResponse=false}
        if (isResponse){ 
            data.Search.forEach((element, i) => {
                title = (data.Search[i].Title);
                img = (data.Search[i].Poster);
                year = (data.Search[i].Year);
                resultSearch[i] = { title, img, year }
            });
    }else{
        resultSearch[0] = { title:'', img:'./img/noresult.png', year:'' };
        }
        return resultSearch;
    }
}
